import request from './http';

// 视频文章详情
export function getArticleDetail(id){
    return request({
        url:"article/"+id,
        method:"get"
    })
}

// 相关视频
export function getArticleNext(id){
    return request({
        url:"article/"+id+"/next",
        method:"get"
    })
}

/**
 * 视频评论
 * @param {Number} resource_id 视频id
 * @param {Number} page 页码
 */
export function getArticleComments(params){
    return request({
        url:"/comments",
        method:"get",
        params
    })
}

// 视频简易详情
export function getArticleShort(mid){
    return request({
        url:"media/"+mid,
        method:"get"
    })
}