<template>
  <div>
    <div class="videocom" v-if="videoData">
      <video-player
        class="video-player vjs-custom-skin"
        ref="videoPlayer"
        :playsinline="true"
        :options="playerOptions"
        style="width: 100%; height: 100%"
        @loadeddata="onPlayerLoadeddata()"
        @timeupdate="onPlayerTimeupdate()"
        @ended="onPlayerEnded()"
        @click="maskShow"
      >
      </video-player>

      <div class="process" v-show="!isMaskShow">
        <div class="line" :style="{ width: lineWidth }"></div>
      </div>
      <div class="clickview" @click="maskShow">
        <div class="videomask" v-show="isMaskShow">
          <div
            :class="['iconfont', isPlay ? 'icon-24gf-play' : 'icon-stop']"
            @click.stop="stateChange"
          ></div>
          <div class="controler">
            <div class="leftTime">{{ leftTime | playerTime }}</div>
            <van-slider
              v-model="currentRate"
              bar-height="3px"
              active-color="#ee0a24"
              @input="sliderChangeFun"
              @drag-start="sliderdragStart"
              @drag-end="sliderdragEnd"
            >
              <template #button>
                <div class="custom-button"></div>
              </template>
            </van-slider>
            <div class="rightTime">{{ rightTime | playerTime }}</div>
            <div class="beisu" @click.stop="show = true">{{ beisuText }}</div>
            <div class="iconfont icon-quanping1"></div>
          </div>
        </div>
        <div class="endview" v-show="isChongBo">
          <img :src="videoData.cover" alt="">
          <div class="playagain">
            <div class="iconfont icon-shuaxin1" @click="playAgain"></div>
            重播
          </div>
        </div>
      </div>
    </div>
    <van-action-sheet
      v-model="show"
      :actions="actions"
      @select="onSelect"
      cancel-text="取消"
      close-on-click-action
      @cancel="show = false"
    />
  </div>
</template>

<script>
import { videoPlayer } from "vue-video-player";
import "video.js/dist/video-js.css";
import { getArticleShort } from "../../api/detail";
export default {
  props: ["mediaId"],
  data() {
    return {
      lineWidth: "50px",
      videoData: null,
      show: false,
      actions: [
        { name: "2.0x", value: 2 },
        { name: "1.5x", value: 1.5 },
        { name: "1.25x", value: 1.25 },
        { name: "1.0x", value: 1 },
        { name: "0.5x", value: 0.5 },
      ],
      rightTime: 0,
      leftTime: 0,
      currentRate: 0, // 进度条
      value: 50,
      isPlay: true, // 播放状态
      beisuText: "倍速",
      playerOptions: {
        playbackRate: 2,
        playbackRates: [0.7, 1.0, 1.5, 2.0], //播放速度
        // autoplay: true, //如果true,浏览器准备好时开始回放。
        muted: true, // 默认情况下将会消除任何音频。
        // loop: true, // 导致视频一结束就重新开始。
        preload: "auto", // 建议浏览器在<video>加载元素后是否应该开始下载视频数据。auto浏览器选择最佳行为,立即开始加载视频（如果浏览器支持）

        language: "zh-CN",

        aspectRatio: "16:9", // 将播放器置于流畅模式，并在计算播放器的动态大小时使用该值。值应该代表一个比例 - 用冒号分隔的两个数字（例如"16:9"或"4:3"）

        fluid: true, // 当true时，Video.js player将拥有流体大小。换句话说，它将按比例缩放以适应其容器。

        sources: [
          {
            type: "video/mp4",

            src: "", //视频url地址
            //imgUrl:require("../assets/test.png")
          },
        ],

        poster: "", //你的封面地址

        notSupportedMessage: "此视频暂无法播放，请稍后再试", //允许覆盖Video.js无法播放媒体源时显示的默认信息。
        controls: false,
      },
      isMaskShow: false,
      isChongBo: false,
    };
  },
  methods: {
    getArticleShortFun() {
      getArticleShort(this.mediaId).then((data) => {
        // console.log("视频播放组件=>", data);
        this.videoData = data.data;
        this.playerOptions.sources[0].src =
          data.data.resource.progressive[0].url;
        this.rightTime = data.data.duration;
        // this.playerOptions.poster = data.data.cover;

      });
    },
    // 播放倍速
    onSelect(item) {
      // 默认情况下点击选项时不会自动收起
      // 可以通过 close-on-click-action 属性开启自动收起
      this.show = false;
      // console.log(item);
      // Toast(item.name);
      let videoDom = document.querySelector(".vjs-tech");
      videoDom.playbackRate = item.value;
      this.beisuText = item.name.toUpperCase();
      this.actions.forEach((i) => {
        delete i.color;
      });
      item.color = "#ee0a24";
    },

    // 改变播放状态
    stateChange() {
      if (this.isPlay) {
        this.$refs.videoPlayer.player.play();
      } else {
        this.$refs.videoPlayer.player.pause();
      }
      this.isPlay = !this.isPlay;
    },

    // 数据完成加载时播放
    onPlayerLoadeddata() {
      // this.stateChange(); //自动播放
      this.$refs.videoPlayer.player.play();
      this.isPlay = !this.isPlay;
    },

    // 实时获取当前播放时间
    onPlayerTimeupdate() {
      let videoDom = document.querySelector(".vjs-tech");
      this.leftTime = videoDom.currentTime;

      // 进度条变化
      this.currentRate = (this.leftTime / this.rightTime) * 100;

      let clientWidth = document.body.clientWidth;

      this.lineWidth = (this.leftTime / this.rightTime) * clientWidth + "px";
      // this.playerOptions.muted = false
    },

    // 播放结束
    onPlayerEnded(){
      // this.isPlay = !this.isPlay;
      this.isChongBo = true;
    },
    // 拉动进度条到某个位置 修改时长
    sliderChangeFun(value) {
      // (进度条位置/100)*总时间
      let currentTime = (value / 100) * this.rightTime;
      this.leftTime = currentTime;
      let videoDom = document.querySelector(".vjs-tech");
      videoDom.currentTime = this.leftTime;
    },
    // 进度条拉动时不播放
    sliderdragStart() {
      // this.$refs.videoPlayer.player.pause();
    },
    // 进度条拉动结束后播放
    sliderdragEnd() {
      // this.$refs.videoPlayer.player.play();
    },
    maskShow() {
      // console.log(11111122312);
      this.isMaskShow = !this.isMaskShow;
    },

    // 重播
    playAgain(){
      this.isChongBo = false;
      this.$refs.videoPlayer.player.play();
    }
  },
  created() {
    this.getArticleShortFun();
  },
  filters: {
    playerTime(data) {
      let m = parseInt(data / 60); //分钟
      let s = parseInt(data % 60); //秒
      if (m == 0 && s == 0) {
        return "0:00";
      } else {
        return m + ":" + (s < 10 ? "0" + s : s); //0:01
      }
    },
  },
  watch:{
    isMaskShow(val){
      if(val==true){
        setTimeout(()=>{
          this.isMaskShow = false;
        },3000)
      }
    }
  },
  components: {
    videoPlayer, //声明
  },
};
</script>

<style lang="less">
.video-js.vjs-fluid {
  height: 100%;
  .vjs-tech {
    object-fit: contain;
  }
}
.videocom {
  width: 100%;
  overflow: hidden;
  position: relative;
  video {
    width: 100%;
    height: auto;
  }
  .process {
    width: 100%;
    height: 3px;
    position: absolute;
    left: 0;
    bottom: -1px;
    background-color: rgba(255, 255, 255, 0.6);
    .line {
      width: 50%;
      height: 2px;
      background-color: #f0472f;
    }
  }
  .clickview{
    position: absolute;
    left: 0;
    top: 0;
    z-index: 500;
    width: 100%;
    height: 100%;
    .videomask {
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3);
    position: absolute;
    left: 0;
    top: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    .icon-24gf-play,
    .icon-stop {
      font-size: 30px;
      color: #fff;
      // position: absolute;
    }
    .controler {
      width: 100%;
      position: absolute;
      left: 0;
      bottom: 10px;
      display: flex;
      color: #fff;
      font-size: 12px;
      align-items: center;
      .leftTime {
        margin-left: 18px;
        margin-right: 12px;
      }
      .rightTime {
        margin-left: 12px;
        margin-right: 8px;
      }
      .beisu {
        width: 45px;
        margin-right: 12px;
        line-height: 16px;
        font-size: 13px;
        position: relative;
        top: -2px;
        white-space: nowrap;
      }
      .iconfont {
        padding-right: 20px;
        position: relative;
        top: -2px;
      }
      .custom-button {
        width: 10px;
        height: 12px;
        background-color: #fff;
        border-radius: 3px;
      }
    }
  }
  .endview {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 502;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    img {
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0;
      top: 0;
      z-index: -1;
      
    }
    .playagain {
      text-align: center;
      font-size: 12px;
      .iconfont {
        font-size: 30px;
        margin-bottom: 4px;
      }
    }

  }
  }
}
</style>