<template>
  <div class="videoplay" v-if="detailData">
    <van-sticky>
      <div class="video">
        <div class="top">
          <div class="iconfont icon-xiangzuojiantou" @click="goPrev"></div>
          <div class="iconfont icon-gengduo1" @click="showShare = true"></div>
        </div>
        <videoview :media-id="mediaId" @click="maskShow"></videoview>
      </div>
      <div class="nav">
        <div>
          <p :class="{ active: navIndex == 0 }" @click="navIndex = 0">简介</p>
        </div>
        <div>
          <p :class="{ active: navIndex == 1 }" @click="navIndex = 1">
            评论 <span v-if="commentTotal">{{ commentTotal }}</span>
          </p>
        </div>
      </div>
    </van-sticky>
    <div class="jianjie" v-show="navIndex == 0">
      <h2>{{ detailData.title }}</h2>
      <p class="info" v-if="detailData.categories[0] || detailData.count">
        <span v-for="item in detailData.categories" :key="item.id + item.sub.id"
          >{{ item.category_name }}-{{ item.sub.category_name }}</span
        >
        <span>{{ (detailData.publish_time * 1000) | changeTimeFormat }}</span>
        <span>{{ detailData.count.count_view | CountHandle }}次播放</span>
      </p>
      <p class="detail">
        {{ detailData.content }}
      </p>
      <div class="iconsAndNum">
        <div>
          <div :class="['iconfont', 'icon-dianzan1', {'likeactive':islike}]" @click="myLike"></div>
          <p>{{ (detailData.count.count_like + addlikecount ) | CountHandle }}</p>
        </div>
        <div>
          <div :class="['iconfont', 'icon-shoucang', {'collectactive':iscollect}]" @click="myCollect"></div>
          <p>{{ (detailData.count.count_collect + addcollectcount) | CountHandle }}</p>
        </div>
        <div>
          <div class="iconfont icon-fenxiang" @click="showShare = true"></div>
          <p>{{ detailData.count.count_share | CountHandle }}</p>
        </div>
      </div>
      <div class="tips zhankai">
        <div class="allTips">
          <span v-for="item in detailData.tags" :key="item.id" class="tip">{{
            item.name
          }}</span>
        </div>
        <!-- <div class="iconfont icon-dianzan1"></div> -->
      </div>
      <div class="author">
        <h3 class="title">创作人</h3>
        <div class="swiper mySwiper authorSwiper" id="authorSwiper">
          <div class="swiper-wrapper">
            <router-link
              tag="div"
              :to="'/userinfo?id=' + detailData.author.userinfo.id"
              class="swiper-slide"
            >
              <div class="img">
                <img :src="detailData.author.userinfo.avatar" alt="" />
              </div>
              <div class="text">
                <p class="name">{{ detailData.author.userinfo.username }}</p>
                <p class="type">{{ detailData.author.role }}</p>
              </div>
              <div class="follow">关注</div>
            </router-link>
            <div class="slideOther">
              <router-link
                tag="div"
                :to="'/userinfo?id=' + item.userinfo.id"
                class="swiper-slide"
                v-for="item in detailData.team"
                :key="item.userinfo.id"
              >
                <div class="img">
                  <img :src="item.userinfo.avatar" alt="" />
                </div>
                <div class="text">
                  <p class="name">{{ item.userinfo.username }}</p>
                  <p class="type">{{ item.role }}</p>
                </div>
                <div class="follow">关注</div>
              </router-link>
            </div>
          </div>
        </div>
      </div>
      <div class="similarVideo" v-if="similarList">
        <h3 class="title">相似视频</h3>
        <router-link
          tag="div"
          :to="'/videoplay?id=' + item.id"
          class="videoInfo"
          v-for="item in similarList"
          :key="item.id"
        >
          <div class="Bigimg">
            <img :src="item.cover" alt="" />
            <div class="time">{{ item.duration | playerTime }}</div>
          </div>
          <div class="detailInfo">
            <h4>{{ item.title }}</h4>
            <p>
              播放 {{ item.count.count_view | CountHandle }} · 喜欢
              {{ item.count.count_like | CountHandle }}
            </p>
            <div class="allAuthor" v-if="item.team[0]">
              <div class="pics">
                <div class="img img1">
                  <img :src="item.team[0].userinfo.avatar" alt="" />
                  <!-- <div>{{item.team[0].userinfo}}</div> -->
                </div>
                <div class="img img2" v-if="item.team[1]">
                  <img :src="item.team[1].userinfo.avatar" alt="" />
                </div>
                <div class="img img3" v-if="item.team[2]">
                  <img :src="item.team[2].userinfo.avatar" alt="" />
                </div>
              </div>
              <div class="all">
                <span class="firstname">{{
                  item.team[0].userinfo.username
                }}</span
                ><span>等{{ item.team_user_count }}位创作人</span>
              </div>
            </div>
            <div class="allAuthor" v-else>
              <div class="pic">
                <div class="img img1">
                  <img :src="item.author.userinfo.avatar" alt="" />
                </div>
              </div>
              <div class="all">{{ item.author.userinfo.username }}</div>
            </div>
          </div>
        </router-link>
      </div>
    </div>
    <div class="comment" v-show="navIndex == 1" v-if="commentList">
      <div class="commentInfo" v-for="item in commentList" :key="item.id">
        <router-link
          tag="div"
          :to="'/userinfo?id=' + item.userInfo.id"
          class="userImg"
        >
          <img :src="item.userInfo.avatar" alt="" />
        </router-link>
        <div class="right">
          <div class="right-top">
            <div class="username">{{ item.userInfo.username }}</div>
            <div class="iconfont icon-gengduo1"></div>
          </div>
          <div class="commentText">
            {{ item.content }}
          </div>
          <div class="right-bottom">
            <p>{{ (item.addtime * 1000) | changeTimeFormat }}</p>
            <div class="like">
              <span class="iconfont icon-dianzan1"></span
              >{{ item.count_approve }}
            </div>
          </div>
        </div>
      </div>

      <div class="noMore">暂时没有更多了</div>
      <div class="inpNull"></div>
      <div class="commitInp">
        <input type="text" placeholder="我有话要说" />
        <div class="submit">发布</div>
      </div>
    </div>
    <van-share-sheet
      v-model="showShare"
      title="立即分享给好友"
      :options="options"
    />
  </div>
</template>

<script>
import Swiper from "swiper";
import "swiper/css/swiper.css";
import videoview from "../components/public/VideoView.vue";
import {
  getArticleDetail,
  getArticleNext,
  getArticleComments,
} from "../api/detail";
export default {
  data() {
    return {
      lineWidth: "50px",
      navIndex: 0,
      detailData: null,
      similarList: null,
      articleId: 0,
      commentList: null,
      page: 1,
      commentTotal: 0,
      mediaId: "",
      loginPhone: "",
      islike: false,
      addlikecount:0,
      iscollect:false,
      addcollectcount:0,
      options: [
        [
          { name: "微信", icon: "wechat" },
          { name: "朋友圈", icon: "wechat-moments" },
          { name: "微博", icon: "weibo" },
          { name: "QQ", icon: "qq" },
        ],
        [
          { name: "复制链接", icon: "link" },
          { name: "分享海报", icon: "poster" },
          { name: "二维码", icon: "qrcode" },
          { name: "小程序码", icon: "weapp-qrcode" },
        ],
      ],
      showShare: false,
    };
  },
  methods: {
    getArticleDetailFun() {
      this.articleId = this.$route.query.id;
      getArticleDetail(this.articleId).then((data) => {
        // console.log("asada", data);
        this.detailData = data.data;
        this.mediaId = data.data.media_id;
        this.ifliked();
      });
    },
    getArticleNextFun() {
      getArticleNext(this.articleId).then((data) => {
        // console.log("相关视频=>",data);
        this.similarList = data.data.list;
      });
    },
    getArticleCommentsFun() {
      getArticleComments({ resource_id: this.articleId, page: this.page }).then(
        (data) => {
          // console.log(data);
          this.commentList = data.data.list;
          this.commentTotal = data.data.total;
        }
      );
    },
    initSwiper() {
      new Swiper("#authorSwiper", {
        loop: true,
        slidesPerView: 4,
        freeMode: true,
        paginationClickable: true,
        spaceBetween: 15,
        observer: true, //修改swiper自己或子元素时，自动初始化swiper
        observeParents: true, //修改swiper的父元素时，自动初始化swipe
      });
    },
    maskShow() {
      // console.log(8208208820);
    },
    goPrev() {
      this.$router.go(-1);
    },
    // 点赞
    myLike() {
      let _this = this;
      if (this.loginPhone == "") {
        this.$dialog
          .confirm({
            title: "未登录",
            message: "是否跳转到登录页面",
          })
          .then(() => {
            _this.$router.push("/loginview");
          });
        return;
      }
      let likeMsg = localStorage.getItem("likeList");
      let likelist = [];
      let articlemsg = {
        articleId: this.articleId,
        cover: this.detailData.cover,
        duration: this.detailData.duration,
        count: this.detailData.count,
        title: this.detailData.title
      };
      if (!likeMsg) {
        likelist.push({ phone: this.loginPhone, articleList: [articlemsg] });
        this.detailData.count.count_like += 1;
        this.islike = true;
      } else {
        likelist = JSON.parse(likeMsg);
        // 是否存在该用户的点赞记录
        let index = likelist.findIndex(
          (item) => item.phone == this.loginPhone
        );
        // console.log("index=>",index);
        if (index == -1) {
          // 不存在点赞记录
          likelist.push({ phone: this.loginPhone, articleList: [articlemsg] });
          // console.log(likelist);
          this.detailData.count.count_like += 1;
          this.islike = true;
        } else {
          // 存在点赞记录
          let articleList = likelist[index].articleList;
          // console.log(articleList);
          let articleIndex = articleList.findIndex(
            (item) => item.articleId == this.articleId
          );
          if (articleIndex == -1) {
            // 没有点赞过该影片
            articleList.push(articlemsg);
            likelist[index].articleList = articleList;
            this.detailData.count.count_like += 1;
            this.islike = true;
          } else {
            // 点赞过该影片 取消点赞
            articleList.splice(articleIndex, 1);
            likelist[index].articleList = articleList;
            this.detailData.count.count_like -= 1;
            this.islike = false;
          }
        }
      }
      likelist = JSON.stringify(likelist);
      localStorage.setItem("likeList", likelist);
    },
    //收藏
    myCollect(){
      let _this = this;
      if (this.loginPhone == "") {
        this.$dialog
          .confirm({
            title: "未登录",
            message: "是否跳转到登录页面",
          })
          .then(() => {
            _this.$router.push("/loginview");
          });
        return;
      }
      let collectMsg = localStorage.getItem("collectList");
      let collectlist = [];
      let articlemsg = {
        articleId: this.articleId,
        cover: this.detailData.cover,
        duration: this.detailData.duration,
        count: this.detailData.count,
        title: this.detailData.title
      };
      if (!collectMsg) {
        collectlist.push({ phone: this.loginPhone, articleList: [articlemsg] });
        this.detailData.count.count_collect += 1;
        this.iscollect = true;
      } else {
        collectlist = JSON.parse(collectMsg);
        // 是否存在该用户的点赞记录
        let index = collectlist.findIndex(
          (item) => item.phone == this.loginPhone
        );
        // console.log("index=>",index);
        if (index == -1) {
          // 不存在点赞记录
          collectlist.push({ phone: this.loginPhone, articleList: [articlemsg] });
          // console.log(collectlist);
          this.detailData.count.count_collect += 1;
          this.iscollect = true;
        } else {
          // 存在点赞记录
          let articleList = collectlist[index].articleList;
          // console.log(articleList);
          let articleIndex = articleList.findIndex(
            (item) => item.articleId == this.articleId
          );
          if (articleIndex == -1) {
            // 没有点赞过该影片
            articleList.push(articlemsg);
            collectlist[index].articleList = articleList;
            this.detailData.count.count_collect += 1;
            this.iscollect = true;
          } else {
            // 点赞过该影片 取消点赞
            articleList.splice(articleIndex, 1);
            collectlist[index].articleList = articleList;
            this.detailData.count.count_collect -= 1;
            this.iscollect = false;
          }
        }
      }
      collectlist = JSON.stringify(collectlist);
      localStorage.setItem("collectList", collectlist);
    },
    findloginPhone() {
      let userMsg = localStorage.getItem("userlist");
      let userlist = [];
      if (!userMsg) {
        return;
      }
      userlist = JSON.parse(userMsg);
      let index = userlist.findIndex((item) => item.type == "login");
      if (index == -1) {
        return;
      }
      this.loginPhone = userlist[index].phone;
    },
    ifliked(){
      let likeMsg = localStorage.getItem("likeList");
      let likelist;
      if (!likeMsg){
        return
      }else{
        likelist = JSON.parse(likeMsg);
        let index = likelist.findIndex(
          (item) => item.phone == this.loginPhone
        );
        if(index==-1){
          return;
        }else{
          // 存在点赞记录
          let articleList = likelist[index].articleList;
          // console.log(articleList);
          let articleIndex = articleList.findIndex(
            (item) => item.articleId == this.articleId
          );
          if (articleIndex == -1){
            this.islike = false;
          }else{
            this.islike = true;
            this.detailData.count.count_like += 1;
          }
        }
      }
    },
    ifcollected(){
      let collectMsg = localStorage.getItem("collectList");
      let collectlist;
      if (!collectMsg){
        return
      }else{
        collectlist = JSON.parse(collectMsg);
        let index = collectlist.findIndex(
          (item) => item.phone == this.loginPhone
        );
        if(index==-1){
          return;
        }else{
          // 存在点赞记录
          let articleList = collectlist[index].articleList;
          // console.log(articleList);
          let articleIndex = articleList.findIndex(
            (item) => item.articleId == this.articleId
          );
          if (articleIndex == -1){
            this.iscollect = false;
            this.addcollectcount = 0;
          }else{
            this.iscollect = true;
            this.addcollectcount = 1;
          }
        }
      }
    }
  },
  created() {
    this.getArticleDetailFun();
    this.getArticleNextFun();
    this.getArticleCommentsFun();
    this.findloginPhone();
    this.ifcollected();
  },
  mounted() {
    this.initSwiper();
  },
  filters: {
    CountHandle(val) {
      if (val < 10000) {
        return val;
      }
      let w = val > 10000 ? val / 10000 : val; //万计算
      return w.toFixed(1) + "w";
    },
    playerTime(data) {
      let m = parseInt(data / 60);
      let s = parseInt(data % 60);
      if (m == 0 && s == 0) {
        return "00'00''";
      } else {
        return (m < 10 ? "0" + m : m) + "'" + (s < 10 ? "0" + s : s) + "''";
      }
    },
    changeTimeFormat(timespan) {
      var dateTime = new Date(timespan); // 将传进来的字符串或者毫秒转为标准时间
      var year = dateTime.getFullYear();
      var month = dateTime.getMonth() + 1;
      var day = dateTime.getDate();
      // var hour = dateTime.getHours();
      // var minute = dateTime.getMinutes();
      // var second = dateTime.getSeconds()
      var millisecond = dateTime.getTime(); // 将当前编辑的时间转换为毫秒
      var now = new Date(); // 获取本机当前的时间
      var nowNew = now.getTime(); // 将本机的时间转换为毫秒
      var milliseconds = 0;
      var timeSpanStr;
      milliseconds = nowNew - millisecond;
      if (
        milliseconds > 1000 * 60 * 60 * 24 * 15 &&
        year === now.getFullYear()
      ) {
        timeSpanStr = month + "-" + day;
      } else {
        timeSpanStr = year + "-" + month + "-" + day;
      }
      return timeSpanStr;
    },
  },
  watch: {
    $route(n, o) {
      if (n.fullPath !== o.fullPath) {
        //监听路由参数是否变化
        this.detailData = null;
        this.similarList = null;
        this.commentList = null;
        this.getArticleDetailFun();
        this.getArticleNextFun();
        this.getArticleCommentsFun();
      }
    },
  },
  components: {
    videoview,
  },
};
</script>


<style lang="less">
.videoplay {
  .video {
    width: 100%;
    position: relative;
    .top {
      width: 100%;
      height: 40px;
      display: flex;
      justify-content: space-between;
      position: absolute;
      top: 0;
      left: 0;
      z-index: 501;
      .iconfont {
        color: #fff;
        line-height: 40px;
      }
      .icon-xiangzuojiantou {
        padding-left: 12px;
        font-weight: bold;
      }
      .icon-gengduo1 {
        padding-right: 16px;
      }
    }
  }
  .nav {
    height: 36px;
    border-bottom: 1px solid #f1f1f1;
    margin-top: -4px;
    background-color: #fff;
    display: flex;
    div {
      flex: 1;
      line-height: 36px;
      font-size: 13px;
      color: #868686;
      p {
        width: 50px;
        margin: 0 auto;
        text-align: center;

        &.active {
          color: #000;
          font-weight: bold;
        }
        &.active::after {
          content: "";
          display: block;
          width: 28px;
          height: 3px;
          margin-left: 10px;
          margin-top: -4px;
          border-radius: 3px;
          background-color: #cb2b14;
        }
        span {
          font-size: 12px;
          color: #d76453;
          font-weight: normal;
        }
      }
    }
  }
  .jianjie {
    h2 {
      padding: 0 21px;
      padding-top: 15px;
      padding-bottom: 12px;
      line-height: 30px;
      font-size: 19px;
      font-weight: 600;
      font-family: "黑体";
    }
    .info {
      font-size: 12px;
      padding-left: 12px;
      color: #a3a3a3;
      span {
        padding: 0 9px;
      }
    }
    .detail {
      line-height: 17px;
      font-size: 12px;
      padding: 0 21px;
      padding-top: 15px;
      color: #3c3c3c;
      // height: 50px;
      // overflow: hidden;
    }
    .iconsAndNum {
      padding: 16px 0;
      display: flex;
      div {
        flex: 1;
        text-align: center;
        .iconfont {
          margin: 0 auto;
          line-height: 28px;
          font-size: 20px;
          font-weight: bold;
        }
        .likeactive {
          color: red;
        }
        .collectactive{
          color: rgb(255, 187, 0);
        }
        .icon-shoucang {
          font-size: 23px;
        }
        p {
          font-size: 12px;
          line-height: 19px;
        }
      }
    }
    .tips {
      padding: 15px 20px 0;
      border-top: 1px solid #f8f8f8;
      border-bottom: 1px solid #f8f8f8;
      display: flex;
      .allTips {
        flex: 1;
        .tip {
          display: inline-block;
          height: 46px;
          line-height: 46px;
          font-size: 18px;
          padding: 0 24px;
          transform: scale(0.5);
          border-radius: 6px;
          background-color: #f7f7f7;
          color: #737373;
          margin-left: -24px;
          margin-right: -12px;
          margin-top: -12px;
          margin-bottom: 3px;
        }
      }
    }
    .zhankai {
      height: 34px;
      overflow: hidden;
    }

    .author {
      width: 100%;
      overflow: hidden;
      .title {
        height: 45px;
        line-height: 45px;
        margin-top: 8px;
        margin-left: 20px;
        font-size: 15px;
        font-weight: bold;
      }
      .authorSwiper {
        padding-bottom: 20px;
        padding-left: 20px;
        padding-right: 20px;
        overflow-y: scroll;
        .slideOther {
          display: flex;
        }
        .swiper-slide {
          width: 272px;
          height: 72px;
          border-radius: 5px;
          display: flex;
          align-items: center;
          box-shadow: -1px 1px 10px 0px #eee;
          .img {
            width: 50px;
            height: 50px;
            margin-left: 10px;
            border-radius: 50%;
            overflow: hidden;
            img {
              width: 50px;
              height: 50px;
            }
          }
          .text {
            flex: 1;
            margin-left: 8px;
            font-size: 14px;
            overflow: hidden;
            .name {
              margin-top: 8px;
              white-space: nowrap;
              overflow: hidden;
            }
            .type {
              color: #a5a5a5;
              font-size: 18px;
              transform: scale(0.5);
              margin-left: -44px;
              margin-right: -38px;
              white-space: nowrap;
              overflow: hidden;
            }
          }
          .follow {
            margin-left: 10px;
            margin-right: 20px;
            width: 72px;
            height: 28px;
            background-color: #f9edec;
            color: #d3513e;
            font-size: 12px;
            line-height: 28px;
            text-align: center;
            border-radius: 14px;
          }
        }
      }
    }
    .similarVideo {
      border-top: 1px solid #f8f8f8;
      .title {
        height: 45px;
        line-height: 45px;
        padding-top: 8px;
        padding-left: 20px;
        font-size: 15px;
        font-weight: bold;
      }
      .videoInfo {
        padding: 0 20px;
        display: flex;
        padding-bottom: 16px;
        .Bigimg {
          width: 160px;
          height: 90px;
          border-radius: 5px;
          overflow: hidden;
          position: relative;
          box-shadow: 0px 4px 4px 0px #ddd;
          img {
            width: 160px;
            height: 90px;
          }
          .time {
            position: absolute;
            right: 8px;
            bottom: 0;
            line-height: 27px;
            font-size: 12px;
            color: #fff;
          }
        }
        .detailInfo {
          flex: 1;
          padding-left: 12px;
          h4 {
            font-size: 13px;
            line-height: 19px;
            white-space: wrap;
            font-weight: bold;
          }
          p {
            height: 26px;
            line-height: 26px;
            font-size: 12px;
